<template>
  <div id="create-shop">
      <el-dialog
      :visible.sync="isDialogVisible"
      :before-close="handleClose"
      :show-close="false"
      size="">
          <div class="dialog-title" slot="title">创建店铺</div>
          <div class="dialog-content">
              <!-- 店铺名称 -->
              <div class="shop-item name clearfix">
                  <div class="shop-title left">
                      <span class="must">店铺名称</span>
                  </div>
                  <div class="shop-content left">
                      <el-input 
                      :class="{errtip: isShownameerr}" 
                      v-model="name"
                      @blur="checkShopName"
                      @change="checkEmptyInput"></el-input>
                  </div>
                  <div v-if="isShownameerr" class="shop-tip left">
                      请输入中文或英文
                  </div>
              </div>

              <!-- 店铺logo -->
              <div class="shop-item logo clearfix">
                    <div class="shop-title left">
                      <span class="must">店铺Logo</span>
                    </div>
                    <div class="shop-content left">
                        <img v-if="logoImg === ''" src="../images/symbols-shanghu-logo_pic_normal.png">
                        <div v-else class="selec-img">
                            <img  :src="imgbase_url + '/img_get.php?img=1&height=100&width=100&imgname=' +logoImg">
                            <span class="picture-actions">
                                <span class="picture-preview">
                                <i class="el-icon-view" @click="isshowPicture"></i>
                                </span>
                                <span class="picture-delete">
                                <i class="el-icon-delete2" @click="deleteimg"></i>
                                </span>
                            </span>
                        </div>
                    </div>
                    <!-- 图片上传  start-->
                    <div class="img-upload left">
                        <s-upload 
                        :ImageSize="imageSize" 
                        @send-image="imgChangeHandler"
                        @on-change="checkEmptyInput"></s-upload>
                        <div class="img-tip">一个月内只能更换一次标志图片，文件类型必须为JPG、JPEG、PNG，宽500像素，高500像素，文件大小不得超过1M</div>
                    </div>
                    <!-- 图片上传 end-->
              </div>

              <!-- 店铺电话 -->
              <div class="shop-item telephone clearfix">
                    <div class="shop-title left">
                        <span class="must">店铺电话</span>
                    </div>
                    <div class="shop-content left">
                      <el-input 
                      v-model="telephone"
                      :class="{errtip: isShowphoneerr}"
                      @keyup.native="checkphone"
                      @change="checkEmptyInput"
                      ></el-input>
                    </div>
                    <div  v-if="isShowphoneerr" class="shop-tip left">
                      请输入联系电话
                  </div>
              </div>

              <!-- 店铺面积 -->
              <div class="shop-item area clearfix">
                    <div class="shop-title left">
                        <span class="must">店铺面积</span>
                    </div>
                    <div class="shop-content left">
                      <el-input 
                      v-model="area" 
                      :class="{errtip: isShowareaerr}"
                      @keyup.native="checkArea"
                      @change="checkEmptyInput"></el-input>
                      <span class="unit">㎡</span>
                    </div>
                    <div  v-if="isShowareaerr" class="shop-tip left">
                      请输入数字
                  </div>
              </div>

              
              <!-- 地址 -->
              <div class="shop-item address clearfix">
                    <div class="shop-title left">
                        <span class="must">地址</span>
                    </div>
                    <div class="shop-content left">
                      <el-input v-model="address"
                      placeholder="省市区街道楼盘名称"
                      @change="checkEmptyInput"></el-input>
                    </div>
                    <div class="map-icon left" @click="openMap">
                      <img src="../images/address.png" alt="">
                    </div>
              </div>

              <!-- 营业模式 -->
                <div class="shop-item business  clearfix">
                    <div class="shop-title left">
                        <span class="must">营业模式</span>
                    </div>
                    <div class="shop-content left clearfix">
                        <div class="btn left" v-for="item in businessOptions"
                        :key="item.value"
                        :class="{active: item.value === business}"
                        @click="changeSelect(item.value)">
                        {{item.label}}
                        </div>
                    </div>
                </div>

                <div class="btn-group">
                    <div  v-if="ishidebtn"  class="btn btn-confrim" @click="confirmCreate">创建</div>
                    <div v-else class="btn btn-confrim gray">创建</div>
                    <div class="btn btn-cancel" @click="handleClose">取消</div>
                </div>
          </div>
      </el-dialog>

      <!-- 图片放大 -->
      <show-img v-if="showPicture" @close-img="isclosePicture" :imgSrc="imgSrc"></show-img>
      <!-- 图片放大 -->  

    <!-- 地图 -->
    <map-component 
    v-if="isShowMap" 
    @on-close="closeMap"
    @get-address="getFormAddress"></map-component>
  </div>
</template>

<script>
import SUpload from "@/components/upload/SUpload";
import ShowImg from "@/components/showImg";
import MapComponent from "@/components/baidumap/index";

import { Util } from "@/config/util";
import { businessType } from "@/config/cfg";
import { createShop } from "../api";
import { ls } from "@/config/pageStore";
import { Public } from "@/config/public";

export default {
  components: {
    SUpload,
    ShowImg,
    MapComponent
  },
  data() {
    return {
      isDialogVisible: false,

      name: "",
      logoImg: "",
      area: "",
      address: "",
      telephone: "",

      business: 1, //营业模式
      businessOptions: [
        {
          value: businessType.FASTFOOD,
          label: businessType.toString(businessType.FASTFOOD)
        },
        {
          value: businessType.CHINESEFOOD,
          label: businessType.toString(businessType.CHINESEFOOD)
        }
      ],

      imageSize: 1024,
      showPicture: false,
      imgSrc: "",

      isShownameerr: false,
      isShowareaerr: false,
      isShowphoneerr: false,

      imgbase_url: imgbase_url,

      isclick: false,

      ishidebtn: false,

      isShowMap: false //地图
    };
  },
  props: {
    dialogVisible: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    dialogVisible: function(val) {
      if (val === true) {
        this.isDialogVisible = true;
      }
    }
  },
  methods: {
    handleClose() {
      this.$emit("on-close");
      this.isDialogVisible = false;
    },
    imgChangeHandler(select) {
      this.logoImg = select;
    },
    deleteimg() {
      this.logoImg = "";
    },
    isshowPicture() {
      this.imgSrc = this.logoImg;
      this.showPicture = true;
    },
    isclosePicture() {
      this.showPicture = false;
    },
    changeSelect(select) {
      this.business = select;
    },
    confirmCreate() {
      // this.$emit("creatshop-sucess");
      // return;
      let data = {
        save: 1,
        shop_name: this.name,
        shop_logo: this.logoImg,
        shop_area: this.area,
        address: this.address,
        shop_model: this.business,
        telephone: this.telephone
      };

      createShop(data, resp => {
        if (resp.ret === 0) {
          let logininfo = Public.getShopinfo();
          let shopinfo = logininfo.shopinfo || [];
          shopinfo.push(resp.data.shopinfo);
          ls.setItem("public#shopinfo", logininfo, 7);
          ls.setItem("public#shopid", resp.data.shopinfo.shop_id, 7);
          this.$emit("creatshop-sucess");
          this.$router.push("/shop/shopinfo");
          console.log("创建成功！");
        } else {
          this.$slnotify({
            message: "创建失败"
          });
          console.log("创建失败！");
        }
      });
    },
    checkArea() {
      this.area = Util.clearNoNum(this.area);
    },
    checkphone() {
      this.telephone = this.checktelephone(this.telephone);
    },
    checkShopName() {
      let reg = /^[a-zA-Z\u4e00-\u9fa5]+$/g;

      if (!reg.test(this.name)) {
        this.isShownameerr = true;
        this.name = "";
      }
    },
    clearTip() {
      this.isShownameerr = false;
    },
    checktelephone(value) {
      value = String(value);
      value = value.replace(/[^\d-]/g, ""); //清除“数字”和“.”以外的字符
      // if (value.indexOf(".") < 0 && value !== "") {
      //   value = parseFloat(value);
      // }
      return value;
    },
    checkEmptyInput() {
      if (
        this.name === "" ||
        this.logoImg === "" ||
        this.telephone === "" ||
        this.area === "" ||
        this.address === ""
      ) {
        this.ishidebtn = false;
      } else {
        this.ishidebtn = true;
      }

      this.clearTip();
    },

    //map
    openMap() {
      this.isShowMap = true;
    },
    closeMap() {
      this.isShowMap = false;
    },
    getFormAddress(resp) {
      this.address = resp;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";
.dialog-title {
  @include wh(640px, 40px);
  @include fc(16px, #fff);
  background-color: #5a8cff;
  text-align: center;
  line-height: 40px;
}
.shop-item {
  margin-bottom: 20px;
}
.shop-title {
  width: 150px;
  text-align: right;
  opacity: 0.5708;
  margin-right: 14px;
}
.shop-tip {
  padding-left: 14px;
  font-size: 12px;
  color: #e7487e;
}
.shop-item.name {
  line-height: 34px;
}
.shop-item.telephone {
  line-height: 34px;
}
.shop-item.logo {
  .shop-content {
    @include wh(100px, 100px);
    img {
      @include wh(100px, 100px);
    }
  }
  .img-upload {
    margin-left: 20px;
    .img-tip {
      font-size: 12px;
      color: #9b9b9b;
      width: 270px;
      padding-top: 22px;
    }
  }
}
.shop-item.area {
  line-height: 34px;
  .shop-content {
    position: relative;
    .unit {
      position: absolute;
      right: -24px;
      bottom: 0;
    }
  }
  .shop-tip {
    padding-left: 36px !important;
  }
}
.shop-item.address {
  line-height: 34px;
  .map-icon {
    margin-left: 10px;
    @include wh(34px, 34px);
    img {
      @include wh(34px, 34px);
    }
  }
}
.shop-item.business {
  line-height: 30px;
  padding-bottom: 40px;
  .btn {
    @include wh(80px, 30px);
    background: #d8d8d8;
    border-radius: 2px;
    font-size: 14px;
    color: #ffffff;
    text-align: center;
    line-height: 30px;
    margin-right: 10px;
    cursor: pointer;
  }
  .btn.active {
    background: #ffffff;
    border: 1px solid #4877e7;
    border-radius: 2px;
    color: #4877e7;
  }
}
.selec-img {
  position: relative;
  @include wh(100px, 100px);
}
.picture-actions {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  cursor: default;
  text-align: center;
  color: #fff;
  opacity: 0;
  font-size: 20px;
  background-color: rgba(0, 0, 0, 0.5);
  transition: opacity 0.3s;

  span {
    line-height: 100px;
  }
  .el-icon-view {
    cursor: pointer;
  }
  .el-icon-delete2 {
    cursor: pointer;
  }
  .picture-delete {
    margin-left: 10px;
  }
}
.selec-img:hover {
  .picture-actions {
    opacity: 1;
  }
}
span.must {
  position: relative;
}
span.must:before {
  position: absolute;
  left: -10px;
  top: 3px;
  content: "";
  display: block;
  @include wh(10px, 10px);
  background: url(../images/must.png);
  background-repeat: no-repeat;
  margin-right: 4px;
}
.btn-group {
  .btn {
    @include wh(160px,40px);
    line-height: 40px;
    text-align: center;
    border-radius: 2px;
    font-size: 14px;
    color: #4877e7;
    display: inline-block;
    border: 1px solid #4877e7;
    cursor: pointer;
  }
  .btn-confrim {
    background: #4877e7;
    color: #fff;
    margin-right: 56px;
    margin-left: 129px;
  }
  .btn-confrim.gray {
    background: #d8d8d8;
    border-color: #d8d8d8;
    cursor: no-drop;
  }
}
</style>

<style lang="scss">
@import "src/styles/mixin.scss";
#create-shop {
  .el-dialog__header {
    padding: 0;
  }
  .el-dialog__body {
    padding: 40px 0 50px;
  }
  .el-input__inner {
    @include wh(280px, 34px);
    border-radius: 0;
  }
  .el-input.errtip .el-input__inner {
    border-color: #e7487e;
  }
  .el-dialog {
    position: absolute;
    transform: none;
    width: 640px;
    margin-left: -320px;
  }
}
</style>



